<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left flex_start">
				<i class="icon iconfont icon-jijinyunhang1"></i>
				<span>{{ name }}</span>
			</div>
		</div>

		<div class="cash_flow_table_box">
			<div class="fundRun-box">
				<div class="fundRun-body-box">
					<div class="fundRun-body-title flex_between">
						<div class="flex_start">
							<span class="circle_blue"></span>
							<span class="name">基金运行</span>
						</div>
					</div>

					<div class="fund_run_nav flex_start">
						<div v-for="(item,index) of navList" :key="index" class="fund_run_nav_list">
							<router-link :to="'/home/fundManageHome/fundDataHome/subFund/subFundRun/'+item.router+'?id='+id">
								{{item.name}}
							</router-link>
						</div>
					</div>

					<div class="fundRun-body-tab-box">
						<router-view></router-view>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getNameApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		data() {
			return {
				id: '',
				name: '',
				navList: [ 
					{
						name: '投资组合',
						step: 1,
						router: 'subFundRunInvestGroup',
					}, 
					{
						name: '认购基金',
						step: 2,
						router: 'subFundRunSubscribe',
					}, 
					{
						name: '现金流',
						step: 3,
						router: 'subFundRunCash',
					}, 
				],
			}
		},
		watch: {

		},
		created() {
			this.id = this.$route.query.id;
			this.getName()
		},
		computed: {},
		mounted() {},
		methods: {
			getName(){
				getNameApi({
					id: this.id,
					type: 4
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.name = res.data.payload.data.name;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../style/cashFlow.scss';
	.body{
		padding: 0 30px 20px 30px;
	}
	.cancle-btn {
		display: inline-block;
		width: 61px;
		height: 23px;
		background-color: #3C98E8;
		color: #ffffff;
		text-align: center;
		line-height: 23px;
		font-size: 14px;
		margin-left: 10px;
		margin-top: 20px;
		cursor: pointer;
	}
	
	.fundRun-box {
		//   min-width: 1100px;
		height: auto;
		//   padding-right: 16px;
	}
	
	.fundRun-body-box {
		width: 100%;
		background-color: #ffffff;
		margin-top: 20px;
		.fundRun-body-title {
			font-size: 16px;
			height: 30px;
			.circle_blue {
				width: 7px;
				height: 7px;
				border-radius: 50%;
				margin-right: 5px;
				background-color: #6292F4;
			}
			.name {
				color: #6292F4;
				font-size: 16px;
			}
			.search-name {
				font-size: 14px;
				padding-right: 10px;
				color: #35383A;
			}
		}
		.fundRun-body-tab-box {
			margin-top: 20px;
			.fundRun-body-tab-title {
				margin-bottom: 10px;
				.cricle {
					display: inline-block;
					width: 6px;
					height: 6px;
					background-color: #3C98E8;
					border-radius: 3px;
					margin-right: 2px;
					margin-left: 20px;
					position: relative;
					top: -2px;
				}
				.fundRun-body-tab-title-name {
					font-size: 14px;
					font-weight: bold;
					color: #35383A;
				}
			}
			.table-normal {
				border: none;
			}
			.table-normal>.noColspanTr>th {
				text-align: left;
				color: #35383A;
				font-weight: 500;
				background-color: #F6FBFF;
				border: none;
			}
			.table-normal>.noColspanTr>td {
				text-align: left;
				border-right: none;
				position: relative;
				&>span {
					color: #6F7071;
					font-size: 14px;
				}
				input {
					width: 110px;
					height: 24px;
					border: 1px solid #F1F1F1;
					text-indent: 10px;
					border-radius: 3px;
					font-size: 14px;
					color: #6F7071;
				}
				input:focus {
					box-shadow: 0px 0px 2px 0px rgba(60, 152, 232, 1);
					border: 1px solid #3C98E8;
				}
				.showIcon {
					display: inline-block;
					width: 12px;
					height: 6px;
					background: url('../../images/slide_shape_off.png') no-repeat center;
					background-size: 100%;
					position: absolute;
					top: 50%;
					right: 18px;
					margin-top: -3px;
					transition: all 200ms linear;
					cursor: pointer;
				}
				.revolveShowIcon {
					transform: rotate(180deg)
				}
			}
			.table-normal>.secondData {
				background-color: #F6FBFF;
			}
			.table-normal>.secondData>td:nth-child(1) {
				&>span {
					padding-left: 30px;
				}
			}
		}
		.commit-btn-box {
			margin-top: 30px;
			.commit-btn {
				display: inline-block;
				width: 100px;
				height: 30px;
				font-size: 14px;
				text-align: center;
				line-height: 30px;
				background-color: #3C98E8;
				color: white;
			}
		}
		.fundRun-body-tab-item-box {
			margin-bottom: 20px;
		}
	} //excel文件导入导出按钮
	.table-group-title-btn {
		line-height: 30px;
		font-size: 14px;
		padding: 0px 10px;
		margin-right: 0px;
		margin-left: 10px;
		position: relative;
		top: 0px;
		&>span {
			display: inline-block;
			width: 20px;
			height: 20px;
			float: left;
			// margin-top: 5px;
			margin-right: 5px;
		}
		.icon_upload {
			background: url('../../images/icon_upload.png') no-repeat;
			background-size: 90%;
		}
		.icon_download {
			background: url('../../images/icon_download.png') no-repeat;
			background-size: 100%;
			// margin-top: 4px;
		}
	}
	
	.fundRun-body-tab-btn {
		display: flex;
		width: 61px;
		height: 23px;
		align-items: center;
		background-color: #3C98E8;
		justify-content: center;
		position: relative;
		top: 22px;
		cursor: pointer;
		img {
			width: 14px;
			height: 14px;
			margin-right: 2px;
		}
		span {
			color: #ffffff;
			font-size: 14px;
		}
	}
	
	.fundRun-body-tab-btn-box1 {
		margin-bottom: 10px;
		.fundRun-body-tab-btn {
			top: 0px;
		}
	}
	.fund_run_nav{
		margin-top: 20px;
		border-top: 1px solid #F1F1F1;
		border-bottom: 1px solid #F1F1F1;
	}
	.fund_run_nav_list{
		height: 67px;
		padding: 0 32px;
		border-bottom: 2px solid white;
	}
	.fund_run_nav_list a{
		display: block;
		height: 67px;
		line-height: 67px;
		box-sizing: border-box;
		padding: 0 3px;
		font-size: 14px;
	}
	.fund_run_nav_list .router-link-active{
		color: #6292F4 !important;
		background: white border-box;
		border-bottom: 2px solid #6292F4;
	}
</style>


